.chat-admin-body {
    margin: 0;
    display: flex;
    flex-direction: column;
    height: 100vh;
    /* Full viewport height */
}

.container-login {
    margin-left: 120px;
    margin-right: 120px;
    padding: 0 20px 0 20px;
    max-width: 100%;
    background: #eef5e8;
    text-align: center;
}

.container-login h1 {
    padding-bottom: 0px;
    margin-bottom: 0px;
}

.container-login p {
    font-size: 20px;
    font-weight: 100;
    margin-top: 10px;
}

.features {
    flex: 1;
    background: #eef5e8;
    color: #000000;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    padding: 40px 0;
    margin-left: 120px;
    margin-right: 120px;
}

.button-login-page {
    color: #d6eadf;
    font-size: 20px;
    margin-bottom: 10px;
    height: 40px;
    background-color: #1c7097;
    width: 20%;
    font-style: oblique;
    border-radius: 5px;
    cursor: pointer;
}

.loginform {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    height: 100%;
    animation: movement-container 0.5s ease-in-out;
}

.form-row {
    display: flex;
    margin: 20px;
}

.form-row input[type="text"],
.form-row input[type="email"],
.form-row input[type="password"] {
    width: 200px;
    border-radius: 5px;
    padding: 2px;
}

#submitbtn {
    margin-top: 10px;
    width: 100%;
    border-radius: 5px;
    background-color: #1c7097;
    color: #d6eadf;
}

.form-row input[type="submit"] {
    margin-top: 10px;
    width: 100%;
    border-radius: 5px;
    background-color: #1c7097;
    color: #d6eadf;
    /* Spacing above the submit button */
}

.form-row label {
    display: table-row;
    width: 100px;
    /* Adjust width as needed */
    text-align: right;
    padding-right: 10px;
}

.form-row input {
    display: table-row;
}

.footer-login-page {
    height: 60px;
    background-color: #333;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
}

.footer-login-page ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.footer-login-page li {
    display: inline-block;
}

.footer-login-page li a {
    padding: 6px;
    font-size: 14px;
    text-decoration: none;
    color: #c3c3c3;
}

.footer-login-page li a:hover {
    color: white;
}

.footer-login-page p {
    padding: 6px;
    margin: 0;
}

.displayoff {
    display: none;
}

@keyframes movement-container {
    0% {
        transform: translateX(100%);
    }

    100% {
        transform: translateX(0);
    }
}

@media only screen and (max-width: 600px) {
    header {
        color: #ffffff;
        background-color: #284b63;
        padding: 20px 0;
        text-align: center;
    }

    header h1 {
        font-size: 30px;
        padding: 0;
        margin: 0;
    }

    header p {
        font-size: 18px;
        font-weight: 100;
    }

    button {
        width: 50%;
    }

    input[type="email"],
    input[type="text"],
    input[type="password"],
    input[type="file"] {
        width: 70%;
    }

    input[type="submit"] {
        color: #f0f0f0;
        font-size: 20px;
        margin-bottom: 10px;
        height: 40px;
        background-color: #1c7097;
        width: 50%;
        font-style: oblique;
        border-radius: 5px;
        cursor: pointer;
    }

    label {
        margin: -10px;
        padding: 0px;
        font-size: 15px;
    }

    textarea {
        width: 70%;
    }

    .queryform input {
        margin: 0px;
    }

    .footer-login-page {
        display: none;
    }
}