body {
    font-family: Arial, sans-serif;
}

header {}

header h1 {
    padding-bottom: 0px;
    margin-bottom: 0px;
}

header p {
    font-size: 20px;
    font-weight: 100;
    margin-top: 10px;
}

.doc_image {
    max-width: 50%;
    justify-content: start;
    height: auto;
    display: block;
}

@media (max-width: 768px) {
    .doc_image {
        max-width: 100%;
    }
}